import React, { useEffect, useRef, useState } from "react";
import LayoutComponent from "../LayoutComponent/LayoutComponent";
import "antd/dist/antd.less";
import "antd/dist/antd.dark.less";
// import "./App.css";
import AppStyle from "./App.module.css";
import "./App.less";
import AppContext, { AppContextType, Context } from "./AppContext";
import EventEmitter from "events";
import { io, Socket } from "socket.io-client";
import { DefaultEventsMap } from "socket.io-client/build/typed-events";
import { openNotification } from "../Notification/Notification";
import {
  Button,
  Form,
  Input,
  message,
  Modal,
  notification,
  Spin,
  Typography,
} from "antd";
import axios from "axios";
import { useBeforeunload } from "react-beforeunload";
import { LoadingOutlined } from "@ant-design/icons";

const { Text } = Typography;

export type ViewType = "streamer" | "viewer";

export let VIEW_TYPE: ViewType;

export const Emitter = new EventEmitter();

export let socketRef: React.MutableRefObject<
  Socket<DefaultEventsMap, DefaultEventsMap> | undefined
>;

// export const HOST: string = "http://localhost:4000/";
export const HOST: string = "/";

let updateNum = 0;
let data: any;

function App() {
  const [update, setUpdate] = useState(updateNum);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [isModal, setIsModal] = useState<boolean>(false);
  const [isNameLoading, setIsNameLoading] = useState<boolean>(false);
  const [inputName, setInputName] = useState<string>("");
  const [contextState, setContextState] = useState<AppContextType>(Context);
  socketRef = useRef<Socket<DefaultEventsMap, DefaultEventsMap>>();

  function forceUpdate() {
    setUpdate(++updateNum);
  }

  useEffect(() => {
    axios
      .get(HOST + Context.url + "/viewtype")
      .then((response) => {
        data = response.data;

        console.log(data);

        if (!data.name) {
          setIsModal(true);
        } else {
          afterNameEdit(data.viewtype, data.name);
        }
      })
      .catch((err) => {
        openNotification({
          message: <Text type="danger">{"Connection error\n"}</Text>,
          description: err.message,
          type: "error",
        });
      });

    return () => {
      socketRef.current?.disconnect();
    };
  }, []);

  const afterNameEdit = (vtype: ViewType, name: string) => {
    VIEW_TYPE = vtype;
    Context.username = name || "user";
    // VIEW_TYPE = window.confirm("Are you streamer") ? "streamer" : "viewer";
    // Context.username = VIEW_TYPE === "streamer" ? "Streamer" : "Viewer";

    setIsLoading(false);

    socketRef.current = io(HOST, {
      reconnection: true,
      transports: ["websocket"],
      query: {
        // roomId: "86dd7a27421",
        roomId: Context.url,
      },
    });

    socketRef.current.on("connect", () => {
      socketRef.current?.emit("viewer_connect", Context.username);
    });
    socketRef.current.on("disconnect", (reason) => {
      console.log("disconnected by", reason);

      socketRef.current?.emit("viewer_disconnect", Context.username);
    });

    socketRef.current.on("connect_error", (err) => {
      openNotification({
        message: <Text type="danger">{"Socket connection error\n"}</Text>,
        description: err.message,
        type: "error",
      });
    });

    // for both
    socketRef.current?.on("chat_message", (data) => {
      Emitter.emit("chat_message", data);
    });

    socketRef.current?.on("viewer_connect", (data) => {
      message.info(`User '${data}'connected`);

      Emitter.emit("viewer_connect");
    });

    socketRef.current?.on("viewer_disconnect", (data) => {
      message.warn(`User '${data}' disconnected`);
    });

    socketRef.current?.on("created_notice", (data) => {
      Emitter.emit("created_notice", data);

      notification.info({ message: `User ${data.author} added new notice!` });
    });

    Emitter.on("new_chat_message", (data) => {
      socketRef.current?.emit("new_chat_message", data);
    });

    Emitter.on("create_notice", (data) => {
      socketRef.current?.emit("create_notice", data);
    });

    // for viewers only
    if (VIEW_TYPE === "viewer") {
      socketRef.current?.on("editor_data_broadcast", (data) => {
        Emitter.emit("editor_data_broadcast", data);
      });

      socketRef.current?.on("output_reloaded", () => {
        Emitter.emit("output_reload");
      });

      socketRef.current?.on("closed_notice", (data) => {
        Emitter.emit("closed_notice", data);
      });
    }

    // for streamer only
    else {
      Emitter.on("output_reload", () => {
        socketRef.current?.emit("output_reload");
      });

      Emitter.on("close_notice", (data) => {
        socketRef.current?.emit("close_notice", data);
      });
    }
  };

  const [form] = Form.useForm();

  const changeName = () => {
    setIsLoading(true);
    axios
      .post(HOST + Context.url + "/changename", {
        name: inputName,
      })
      .then(() => {})
      .catch(() => {
        notification.error({
          message: <Text type="danger">Set name error</Text>,
          description:
            "Some error on server. You will be redirect to main page after 4 second!",
          placement: "bottomLeft",
        });

        setTimeout(() => window.location.replace(HOST), 4000);
      })
      .finally(() => {
        setIsLoading(false);
        setIsModal(false);
        form.resetFields();
      });
  };

  useBeforeunload(() => {
    axios.post(HOST + Context.url + "/disconnect");
    socketRef.current?.disconnect();

    return "Youll lose your data!";
  });

  return (
    <div className={`App ${AppStyle.Dark}`}>
      <Modal
        visible={isModal}
        afterClose={() => afterNameEdit(data.viewtype, inputName)}
        title={"Enter your name"}
        centered
        footer={[
          <Button
            className="AppButton"
            key="start"
            type="primary"
            htmlType="submit"
            form="name_edit"
            disabled={isNameLoading}
          >
            Ok
          </Button>,
        ]}
      >
        {isNameLoading ? (
          <div
            style={{
              width: "100%",
              height: "100%",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              fontSize: "8em",
            }}
          >
            <LoadingOutlined />
          </div>
        ) : (
          <Form form={form} onFinish={changeName} name="name_edit">
            <Form.Item
              name="Name"
              label="Type your name here"
              rules={[{ required: true }]}
            >
              <Input
                value={inputName}
                onChange={(e) => setInputName(e.target.value)}
                allowClear
              ></Input>
            </Form.Item>
          </Form>
        )}
      </Modal>
      {isLoading ? (
        <div
          style={{
            width: "100%",
            height: "100%",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Spin className="Spin" size="large" />
        </div>
      ) : (
        <AppContext.Provider
          value={{ context: contextState, setContext: setContextState }}
        >
          <LayoutComponent />
        </AppContext.Provider>
      )}
    </div>
  );
}

export default App;
