import {
  AudioMutedOutlined,
  AudioOutlined,
  CloseOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import {
  Badge,
  Button,
  Form,
  Input,
  List,
  Modal,
  notification,
  Space,
  Switch,
  Typography,
} from "antd";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { Emitter, HOST } from "../App/App";
import AppStyle from "../App/App.module.css";
import { Context } from "../App/AppContext";

const { Paragraph, Text } = Typography;

interface User {
  name: string;
  muted: boolean;
  isLoading: boolean;
  _id: string;
}

interface UserWhiteList {
  name: string;
  _id: string;
}

const host = "http://livecoding/";

const Settings = () => {
  const [projectName, setProjectName] = useState<string>("");
  const [projectType, setProjectType] = useState<string>("");
  const [link, setLink] = useState<string>("nf62hd8xbsgj");
  const [isWhiteList, setIsWhiteList] = useState<boolean>(false);
  const [viewersArr, setViewersArr] = useState<User[]>([]);
  const [whiteListArr, setWhiteListArr] = useState<UserWhiteList[]>([]);
  const [isProjectNameLoading, setIsProjectNameLoading] =
    useState<boolean>(true);
  const [isProjectTypeLoading, setIsProjectTypeLoading] =
    useState<boolean>(true);
  const [isProjectLinkLoading, setIsProjectLinkLoading] =
    useState<boolean>(true);
  const [isProjectViewersLoading, setIsProjectViewersLoading] =
    useState<boolean>(true);
  const [isChangeMuteLoading, setIsChangeMuteLoading] =
    useState<boolean>(false);
  const [isWhiteListLoading, setIsWhiteListLoading] = useState<boolean>(true);
  const [isAddButtonLoading, setIsAddButtonLoading] = useState<boolean>(false);
  const [isAddModal, setIsAddModal] = useState<boolean>(false);
  const [isAddLoading, setIsAddLoading] = useState<boolean>(false);
  const [userIdValue, setUserIdValue] = useState<string>("");

  useEffect(() => {
    axios
      .get(HOST + Context.url + "/projectname")
      .then((response) => {
        setProjectName(response.data);
      })
      .catch((err) =>
        notification.error({
          message: <Text type="danger">Project name fetch error</Text>,
          description:
            "Some error on server. Try to reload page!\n" + err.message,
          placement: "bottomLeft",
        })
      )
      .finally(() => setIsProjectNameLoading(false));

    axios
      .get(HOST + Context.url + "/projecttype")
      .then((response) => {
        setProjectType(response.data);
      })
      .catch((err) =>
        notification.error({
          message: <Text type="danger">Project type fetch error</Text>,
          description:
            "Some error on server. Try to reload page!\n" + err.message,
          placement: "bottomLeft",
        })
      )
      .finally(() => setIsProjectTypeLoading(false));

    axios
      .get(HOST + Context.url + "/projectlink")
      .then((response) => {
        setLink(response.data);
      })
      .catch((err) =>
        notification.error({
          message: <Text type="danger">Project link fetch error</Text>,
          description:
            "Some error on server. Try to reload page!\n" + err.message,
          placement: "bottomLeft",
        })
      )
      .finally(() => setIsProjectLinkLoading(false));

    fetchViewers();

    fetchWhiteList();

    Emitter.on("viewer_connect", () => {
      setIsProjectViewersLoading(true);
      fetchViewers();
    });

    Emitter.on("whitelist", () => {
      setIsWhiteListLoading(true);
      fetchWhiteList();
    });
  }, []);

  function fetchViewers() {
    axios
      .get(HOST + Context.url + "/projectviewers")
      .then((response) => {
        setViewersArr(response.data);
      })
      .catch((err) =>
        notification.error({
          message: <Text type="danger">Project viewer fetch error</Text>,
          description:
            "Some error on server. Try to reload page!\n" + err.message,
          placement: "bottomLeft",
        })
      )
      .finally(() => setIsProjectViewersLoading(false));
  }

  function fetchWhiteList() {
    axios
      .get(HOST + Context.url + "/projectwhitelist")
      .then((response) => {
        if (response.data) {
          console.log(response.data);
          setIsWhiteList(true);
          setWhiteListArr(response.data);
        } else {
          setIsWhiteList(false);
        }
      })
      .catch((err) =>
        notification.error({
          message: <Text type="danger">Project white list fetch error</Text>,
          description:
            "Some error on server. Try to reload page!\n" + err.message,
          placement: "bottomLeft",
        })
      )
      .finally(() => setIsWhiteListLoading(false));
  }

  const setProjectNameToServer = (value: string) => {
    setIsProjectNameLoading(true);

    axios
      .post(HOST + Context.url + "/changepojectname", {
        value,
      })
      .then(() => setProjectName(value))
      .catch((err) =>
        notification.error({
          message: <Text type="danger">Project name change error</Text>,
          description:
            "Some error on server. Try to reload page!\n" + err.message,
          placement: "bottomLeft",
        })
      )
      .finally(() => setIsProjectNameLoading(false));
  };

  const changeMute = (name: string) => {
    const user = viewersArr.filter((item) => item.name === name)[0];

    user.isLoading = true;

    setViewersArr(viewersArr);

    axios
      .post(HOST + Context.url + "/changemute", { name })
      .then((response) => {
        user.muted = response.data;

        setViewersArr(
          viewersArr.map((item) => ({ ...item, isLoading: false }))
        );
      })
      .catch((err) =>
        notification.error({
          message: <Text type="danger">User mute change error</Text>,
          description:
            "Some error on server. Try to reload page!\n" + err.message,
          placement: "bottomLeft",
        })
      )
      .finally(() => setIsChangeMuteLoading(true));
  };

  const changeIsWhiteList = () => {
    setIsWhiteListLoading(true);

    axios
      .post(HOST + Context.url + "/changewhitelist")
      .then((response) => {
        setIsWhiteList(response.data);
      })
      .catch((err) =>
        notification.error({
          message: <Text type="danger">White list change error</Text>,
          description:
            "Some error on server. Try to reload page!\n" + err.message,
          placement: "bottomLeft",
        })
      )
      .finally(() => setIsWhiteListLoading(false));
  };

  const onAddToListClick = () => {
    axios
      .post(HOST + Context.url + "/addtowhitelist", {})
      .then(() => {
        setIsWhiteListLoading(true);
        fetchWhiteList();
        notification.info({
          message: "User added to white list",
          placement: "bottomLeft",
        });
      })
      .catch((err) => {
        notification.error({
          message: <Text type="danger">Add to white list error</Text>,
          description:
            "Some error on server. Try to reload page!\n" + err.message,
          placement: "bottomLeft",
        });
      })
      .finally(() => {
        setIsAddModal(false);
        setIsAddButtonLoading(false);
      });
  };

  const onAddClick = () => {
    onAddToListClick();
  };

  const onBackModalClick = () => {
    setIsAddModal(false);
    setIsAddButtonLoading(false);
    form.resetFields();
  };

  const [form] = Form.useForm();

  return (
    <div
      className={AppStyle.Dark}
      style={{ width: "100%", height: "100%", overflow: "overlay" }}
    >
      <Modal
        title="Add user to white list"
        visible={isAddModal}
        onCancel={onBackModalClick}
        footer={[
          <Button key="back" onClick={onBackModalClick} disabled={isAddLoading}>
            Close
          </Button>,
          <Button
            className="AppButton"
            key="start"
            type="primary"
            htmlType="submit"
            form="add_to_white_list"
            disabled={isAddLoading}
          >
            Add
          </Button>,
        ]}
      >
        {isAddLoading ? (
          <div
            style={{
              width: "100%",
              height: "100%",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              fontSize: "8em",
            }}
          >
            <LoadingOutlined />
          </div>
        ) : (
          <Form onFinish={onAddClick} name={"add_to_white_list"} form={form}>
            <Form.Item
              name="User ID"
              label={<Text style={{ fontSize: "1.2em" }}>Type user id</Text>}
              rules={[{ required: true }]}
            >
              <Input
                value={userIdValue}
                onChange={(e) => setUserIdValue(e.target.value)}
                allowClear
              />
            </Form.Item>
          </Form>
        )}
      </Modal>
      <Space
        direction="vertical"
        align="center"
        style={{ width: "100%", paddingTop: 20 }}
        size="large"
      >
        <Space direction="vertical" size="small" style={{ width: 200 }}>
          <div style={{ display: "flex", alignItems: "center" }}>
            <Text style={{ fontSize: 20, marginRight: 20 }}>Project name </Text>
            {isProjectNameLoading && (
              <LoadingOutlined style={{ fontSize: 20 }} />
            )}
          </div>
          <Paragraph
            editable={
              !isProjectNameLoading && { onChange: setProjectNameToServer }
            }
            disabled={isProjectNameLoading}
          >
            {isProjectNameLoading ? <LoadingOutlined /> : projectName}
          </Paragraph>
        </Space>
        <Space direction="vertical" size="small" style={{ width: 200 }}>
          <div style={{ display: "flex", alignItems: "center" }}>
            <Text style={{ fontSize: 20, marginRight: 20 }}>Project type </Text>
            {isProjectTypeLoading && (
              <LoadingOutlined style={{ fontSize: 20 }} />
            )}
          </div>
          <Text code style={{ fontSize: 17 }}>
            {isProjectTypeLoading ? <LoadingOutlined /> : projectType}
          </Text>
        </Space>
        <Space direction="vertical" size="small" style={{ width: 200 }}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            <Text
              style={{
                fontSize: 20,
              }}
            >
              Link
            </Text>
            <Text
              type="success"
              style={
                isProjectLinkLoading
                  ? {
                      padding: "1px 10px",
                      border: "1px solid #fff",
                      color: "#fff",
                      borderRadius: 4,
                      marginTop: 5,
                      cursor: "pointer",
                    }
                  : {
                      padding: "1px 10px",
                      border: "1px solid #0f0",
                      color: "#0f0",
                      borderRadius: 4,
                      marginTop: 5,
                      cursor: "pointer",
                    }
              }
            >
              {isProjectLinkLoading ? <LoadingOutlined /> : "Online"}
            </Text>
          </div>
          <Paragraph>
            <Text style={{ fontSize: 13 }}>{host}</Text>
            <Paragraph
              strong
              copyable={!isProjectLinkLoading && { text: host + link }}
              style={{
                border: "1px solid #fff",
                borderRadius: 5,
                width: "max-content",
                padding: "1px 10px",
                fontSize: 18,
                marginTop: 6,
              }}
            >
              {isProjectLinkLoading ? <LoadingOutlined /> : link}
            </Paragraph>
          </Paragraph>
        </Space>
        <Space direction="vertical" size="small" style={{ width: 200 }}>
          <Badge
            count={viewersArr.length}
            size="default"
            offset={[-15, 5]}
            style={{
              backgroundColor: "rgba(20,20,20,0.6)",
              borderColor: "rgba(0,0,0,0)",
            }}
          >
            <div style={{ display: "flex", alignItems: "center" }}>
              <Text style={{ fontSize: 20, marginRight: 20 }}>Viewers</Text>
              {isProjectViewersLoading && (
                <LoadingOutlined style={{ fontSize: 20 }} />
              )}
            </div>
          </Badge>

          <List
            loading={isProjectViewersLoading}
            dataSource={viewersArr}
            renderItem={(item) => (
              <List.Item>
                <Text>{item.name}</Text>
                <Button
                  onClick={(e) => {
                    changeMute(item.name);
                  }}
                  danger={!item.isLoading && item.muted}
                >
                  {item.isLoading ? (
                    <LoadingOutlined />
                  ) : item.muted ? (
                    <>
                      Unmute <AudioMutedOutlined style={{ marginLeft: 10 }} />
                    </>
                  ) : (
                    <>
                      Mute
                      <AudioOutlined style={{ marginLeft: 10 }} />
                    </>
                  )}
                </Button>
              </List.Item>
            )}
            bordered
            size="small"
            style={{ maxHeight: 150, overflow: "overlay" }}
          />
        </Space>
        <Space direction="vertical" size="small" style={{ width: 200 }}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            <Text style={{ fontSize: 20, marginRight: 20 }}>White list</Text>{" "}
            {isWhiteListLoading ? (
              <LoadingOutlined />
            ) : (
              <Switch
                defaultChecked={isWhiteList}
                onChange={() => changeIsWhiteList()}
              />
            )}
          </div>
          {isWhiteList && (
            <>
              <List
                loading={isWhiteListLoading}
                dataSource={whiteListArr}
                renderItem={(item) => (
                  <List.Item>
                    <Text>{item.name}</Text>
                    <Button
                      onClick={(e) =>
                        setWhiteListArr(
                          whiteListArr.filter(
                            (item_in) => item._id !== item_in._id
                          )
                        )
                      }
                      danger={true}
                    >
                      <CloseOutlined />
                    </Button>
                  </List.Item>
                )}
                bordered
                size="small"
                style={{ height: 150, overflow: "overlay" }}
              />
              <Button
                style={{ width: "100%", marginBottom: 20 }}
                onClick={() => {
                  setIsAddButtonLoading(true);
                  setIsAddModal(true);
                }}
              >
                {isAddButtonLoading ? <LoadingOutlined /> : "Add to list"}
              </Button>
            </>
          )}
        </Space>
      </Space>
    </div>
  );
};

export default Settings;
